footer {
  background: var(--color-primary);
  padding: 2.8rem 0;
  font-size: 0.9rem;
  margin-top: 1.5rem;
  text-align: center;
}
footer a {
  color: var(--color-bg);
}

.footer_logo {
  font-size: 2rem;
  font-weight: 400;
  margin-bottom: 1rem;
  display: inline-block;
}

.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 1rem;
}

.footer_socials {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 1rem;
}

.footer_socials a {
  background: var(--color-bg);
  color: var(--color-white);
  padding: 0.8rem;
  border-radius: 0.7rem;
  display: flex;
  margin: 1px solid transparent;
}

.footer_socials a:hover {
  background: transparent;
  color: var(--color-bg);
  border-radius: var(--color-bg);
}

.footer_copyright {
  margin-bottom: 3.5rem;
  color: var(--color-bg);
  font-size: 0.9rem;
}

/* ============== MEDIA QURIES FOR SMALL DEVICES============ */

@media screen and (max-width: 600px) {
  .permalinks {
    flex-direction: column;
    gap: 1.4rem;
  }
  .footer_socials {
    margin-bottom: 2.6rem;
  }
}
